.subscription {
  width: 1750px;
  margin: 0 auto;
  padding-top: 125px;
  background-color: #E59292;
  background-image: url('../img/subscription_bg_desktop.png');
  background-repeat: no-repeat;
  background-position: center right;

  .row {
    display: flex;
    align-items: flex-start;
    margin-bottom: 50px;

    .up_row {
      display: inline-block;
    }

    .down_row {
      display: none;
    }
  }

  .title {
    color: #FCF6F3;
    margin-bottom: 37px;
  }

  .text {
    color: #ffffff;
    margin-bottom: 68px;
  }

  .input {
    width: 328px;
    height: 55px;
    box-sizing: border-box;
    padding-left: 25px;
    background-color: #ffffff;
    border: none;
    margin-right: 30px;
  }

  .submit {
    min-width: 268px;
    min-height: 55px;
    margin-left: 10px;
    padding: 0;
  }

  .main__checkbox_label {
    display: inline-block;
    width: 610px;
    margin-bottom: 130px;

    .link {
      font-size: 14px;
      line-height: 200%;
      color: #FFFFFF;
    }
  }
}

@media (max-width: $desktop) {
  .subscription {
    width: 100%;
    padding-top: 85px;
    padding-bottom: 40px;
    background-size: 327px 290px;

    .title {
      margin-bottom: 35px;
    }

    .text {
      margin-bottom: 45px;
    }

    .row {
      margin-bottom: 37px;

      .up_row {
        display: none;
      }

      .down_row {
        display: inline-block;
        width: 328px;
        margin-right: 30px;
        margin-left: 0;
      }

      .main__checkbox_label {
        top: -2px;
        width: 310px;
        margin-bottom: 0;
        line-height: 120%;

        &::after {
          top: 14px;
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .subscription {
    padding-top: 55px;
    background-size: 24vw 21vw;
    background-position: 100% 25%;

    .wrapper {
      box-sizing: border-box;
      padding-left: 35px;
    }

    .title {
      margin-bottom: 23px;
    }

    .input {
      width: 313px;
    }

    .row {

      .main__checkbox_label {
        width: 260px;
      }

      .down_row {
        width: 313px;
      }
    }
  }
}

@media (max-width: $mobile) {
  .subscription {
    width: 100vw;
    margin: 0 auto;
    background-position: 100% 33%;
    background-size: 56vw 50vw;
    padding-top: 17vw;
    padding-bottom: 16vw;

    .wrapper {
      padding-left: 0;
    }

    .title {
      margin-bottom: 4vw;
    }

    .text {
      width: 69vw;
      font-size: 4.5vw;
      line-height: 200%;
      margin-bottom: 10vw;
    }

    .row {
      flex-direction: column;
      margin-bottom: 2vw;

      .input {
        width: 100%;
        height: 19vw;
        margin-bottom: 6vw;
        margin-right: 0;
      }

      .submit {
        width: 100%;
        height: 19vw;
        margin-bottom: 6vw;
        margin-right: 0;
      }

      .main__checkbox_label {
        width: 81vw;
        font-size: 4vw;
        line-height: 150%;

        &::after {
          top: 7vw;
        }

        .link {
          font-size: 4vw;
          line-height: 150%;
        }
      }
    }
  }
}
