    .payment .form .pay_list .label {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 224px;
      min-height: 130px;
      box-sizing: border-box;
      padding-top: 25px;
      padding-left: 30px;
      padding-right: 35px;
      padding-bottom: 20px;
      background-color: #F3EDE9;
      outline: 1px solid #DADADA; }
    .payment .form .pay_list .input {
      display: none; }
