      font-weight: normal;
      font-size: 16px;
      line-height: 160%;
      color: #354F62;
      text-transform: none;
      margin: 0; }
    .payment .form .pay_list .input:checked + .label {
      background-color: #528491;
      box-shadow: 0px 25px 25px -20px #528491;
      outline: none;
      transition: 1s; }
      .payment .form .pay_list .input:checked + .label .title {
